@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
.poppins-regular {
    font-family: "Poppins", sans-serif;
    font-weight: 400;
    font-style: normal;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins";
}
#root{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}
body{
    background:url("D:\Yashvanth\WEB DEVELOPMENT\React\qrcode\public\background.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.app-container{
    text-align: center;
    background: linear-gradient(135deg,rgba(255, 255, 255,0.1),rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255,0.18);
    box-shadow: 0px 0px 32px 0 rgba(0,0,0,0.37);
    padding: 35px;
    border-radius: 20px;
    color: whitesmoke;
    text-shadow: 0px 0px 3px;
}
.qr-image{
    box-shadow:0px 0px 15px black;
    border-radius: 20px;
    margin: auto;
    margin-top: 15px;
    display: block;
    height: 250px;
    background-color: whitesmoke;
}
.input-label{
    text-align: left;
    font-weight: bold;
    display: block;
    margin:15px 0 5px 0;
    text-shadow: 0px 0px 2px;
}
input[type="text"]{
    color: white;
    text-shadow: 0px 0px 5px;
    border-radius: 5px;
    outline: none;
    padding: 10px;
    background-color: transparent;
    box-shadow: 0px 0px 32px 0 rgba(0,0,0,0.37);
    width: 100%;
}
.button{
    border-radius: 5px;
    padding: 8px;
    display: flex;
    justify-content: center;
    gap: 10px;
}
button{
    padding: 8px;
    margin-top: 10%;
    border: 0;
    border-radius: 10px;
    background: linear-gradient(135deg,rgba(255, 255, 255,0.1),rgba(255,255,255,0));
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255,0.18);
    box-shadow: 0px 0px 10px 0 rgba(0,0,0,0.37);
    color: whitesmoke;
    font-size: 15px;
    text-shadow: 0px 0px 10px;
    transition: 200ms ease;
    cursor: pointer;
}
button:hover{
    color:greenyellow;
    scale: 1.04;
}
.generate-btn:disabled{
    color: orange;
    cursor: not-allowed;
}
